@media (max-width: 1200px) {
    .menu {
        &_item {
            padding-right: 20px; }
        &_link {
            font-size: 13px; } }
    .promo {
        &_header {
            font-size: 50px; }
        &_subheader {
            font-size: 30px; } }
    .reasons {
        &_block {
            align-items: flex-start; }
        &_descr {
            width: 300px; } }
    .mobile {
        &_item {
            width: 300px; } }
    .car {
        .title {
            &_white {
                font-size: 25px; } } }
    .require {
        .title {
            font-size: 25px; } }
    .map {
        .title {
            &_white {
                font-size: 25px; } } } }

@media (max-width: 991px) {
    .menu {
        &_item {
            padding-right: 8px; }
        &_link {
            font-size: 10px; } }
    .subheader {
        &_logo {
            img {
                width: 100%; } }
        &_official {
            font-size: 11px; }
        &_phone {
            font-size: 18px; }
        &_btn {
            width: 100%;
            height: 40px;
            margin-top: 21px; } }
    .promo {
        &_header {
            font-size: 40px; }
        &_subheader {
            font-size: 25px; } }
    .reasons {
        .title {
            font-size: 25px; }
        &_block {
            flex-direction: column;
            align-items: center; }
        &_round {
            margin-right: 0; }
        &_descr {
            width: 300px;
            margin-top: 20px; } }
    .mobile {
        .title {
            font-size: 25px;
            margin-top: 20px; }
        .subtitle {
            font-size: 17px;
            margin-top: 16px; }
        &_item {
            width: 290px;
            &_subtitle {
                font-size: 18px;
                line-height: 24px; } } }
    .car {
        min-height: 700px;
        padding: 60px 0 20px 0;
        .title {
            &_white {
                font-size: 20px;
                margin-top: 15px; } }
        .subtitle {
            &_white {
                margin-top: 17px; } }
        &_img {
            width: 100%; } }
    .require {
        .title {
            font-size: 20px;
            margin-top: 17px; } }
    .map {
        .title {
            &_white {
                font-size: 20px;
                margin-top: 20px; } }
        .subtitle {
            &_white {
                font-size: 15px;
                margin-top: 10px; } } }
    footer {
        .footer_wrapper {
            flex-wrap: wrap; }
        .footer_wrapper > div {
            display: flex;
            width: 100%;
            margin-bottom: 15px;
            justify-content: center;
            &:nth-child(1) {
                width: 100%; }
            &:nth-child(3) {
                width: 100%;
                justify-content: center; } }
        .footer_mobile {
            a {
                margin: 0 2px; } } } }





@media (max-width: 767px) {
    .menu {
        position: fixed;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        top: 0;
        left: -100%;
        width: 50%;
        height: 100vh;
        padding: 20px 15px;
        background-color: rgba(0,0,0, .8);
        transition: 0.6s all;
        &_active {
            left: 0; }
        &_item {
            padding-right: 0;
            border: none;
            margin-top: 10px; }
        &_link {
            font-size: 20px; } }
    .hamburger {
        display: block;
        position: absolute;
        right: 10px;
        top: 50%;
        transform: translateY(-50%);
        width: 26px;
        height: 12px;
        span {
            display: block;
            height: 2px;
            width: 100%;
            background-color: #fff;
            margin-bottom: 2px;
            transition: 0.5s all; }
        &_active {
            span {
                margin-bottom: -2px;
                &:nth-child(1) {
                    transform: translateY(3px) rotate(-45deg); }
                &:nth-child(2) {
                    display: none; }
                &:nth-child(3) {
                    transform:  translateY(3px) rotate(45deg); } } } }
    .xs-hidden {
        display: none; }
    .xs-visible {
        display: block; }
    .subheader {
        &_phone {
            display: block;
            margin-top: 10px;
            color: #ffffff;
            font-size: 16px;
            font-weight: 400;
            text-transform: uppercase;
            text-align: center; }
        &_btn {
            display: block;
            width: 200px;
            height: 40px;
            margin: 0 auto;
            margin-top: 10px; } }
    .promo {
        &_header {
            font-size: 30px;
            line-height: 45px; } }

    .mobile {
        .title {
            font-size: 25px; }
        &_item {
            width: 280px;
            &_subtitle {
                font-size: 20px; } } }
    .car {
        &_img {
            width: 100%; } }
    .require {
        &_title {
            text-align: center; } }
    .map {
        min-height: 450px; } }


@media (max-width: 575px) {
    nav {
        height: 30px; }
    header {
        height: 30px; }
    .subheader {
        height: 60px;
        top: 30px;
        &_official {
            display: none; }
        &_logo {
            margin-top: 16px; }
        &_phone {
            font-weight: 400;
            text-transform: uppercase;
            margin-top: 11px; }
        &_btn {
            width: 110px;
            height: 22px;
            font-size: 10px;
            margin-top: 6px;
            &:hover {
                background-image: linear-gradient(to top, $main-color 0%, #4ce2ff 100%); } } }
    .promo {
        min-height: 400px;
        padding: 93px 0 35px 0;
        &_descr {
            display: none; }
        &_header {
            font-size: 30px;
            padding: 0 24px;
            line-height: 35px; }
        &_subheader {
            font-size: 18px;
            margin-top: 33px;
            padding: 0 10px;
            line-height: 25px; }
        &_btn {
            width: 210px;
            height: 50px;
            margin-top: 72px;
            &:hover {
                background-image: linear-gradient(to top, $main-color 0%, #4ce2ff 100%); } } }
    .menu {
        width: 85%;
        &_item {
            width: 100%;
            text-align: center;
            padding: 5px 0; }
        &_link {
            display: block; } }
    .reasons {
        padding: 22px 0 36px 0;
        &_block {
            margin-top: 26px; }
        &_round {
            width: 100px;
            height: 100px; }
        &_descr {
            width: 100%;
            margin-top: 27px; }
        &_subtitle {
            font-size: 16px; }
        .title {
            font-size: 14px;
            margin-top: 15px;
            line-height: 16px; } }
    .mobile {
        padding: 25px 0 20px 0;
        &_item {
            width: 290px;
            height: 254px;
            &_subtitle {
                width: 226px;
                display: block;
                margin: 0 auto; } }
        .title {
            font-size: 16px;
            margin-top: 7px; }
        .subtitle {
            font-size: 13px;
            margin-top: 7px; } }
    .car {
        padding: 25px 0 20px 0;
        background: none;
        background-color: #070716;
        min-height: 393px;
        .title {
            display: none; }
        .subtitle {
            display: none; }
        .under_title {
            display: block;
            margin: 0 auto;
            color: white;
            line-height: 24px;
            width: 260px;
            height: 64px;
            br {
                display: none; } } }

    .require {
        padding: 25px 0 20px 0;
        .title {
            font-size: 18px;
            margin-top: 15px; }
        &_title {
            font-size: 16px;
            text-align: left; } }
    .require_list {
        padding-left: 40px;
        li {
            font-size: 14px;
            margin-bottom: 0; } }
    .map {
        padding: 30px;
        min-height: 360px;
        .label {
            &_white {
                font-size: 9px;
                width: 70px;
                height: 24px; } }
        .title {
            &_white {
                font-size: 16px;
                margin-top: 14px; } }
        .subtitle {
            &_white {
                font-size: 14px; } }
        &_link {
            width: 200px;
            display: block;
            margin-bottom: 10px; } }
    footer {
        .footer_wrapper {
            margin-top: 10px; }
        .footer_wrapper > div {
            margin-bottom: 20px;
            &:nth-child(1) {
                order: 2; }
            &:nth-child(2) {
                order: 1; }
            &:nth-child(3) {
                order: 3; } } } }











