header {
  position: relative;
  height: 50px; }

nav {
  position: fixed;
  width: 100%;
  height: 50px;
  background-color: #212121;
  z-index: 2; }

.hamburger {
    display: none; }

.menu {
  display: flex;
  justify-content: space-between;
  padding: 13px 70px;
  margin: 0;
  list-style-type: none;

  &_link {
    color: #ffffff;
    font-size: 15px;
    font-weight: 300; }

  &_item {
    border-right: 2px dotted black;
    padding-right: 28px;
    &:last-child {
        border-right: none; } } }

.subheader {
  position: absolute;
  top: 50px;
  width: 100%;
  height: 84px;
  background-color: rgba(0, 0, 0, 0.6);

  &_logo {
    display: block;
    max-width: 100%;
    margin-top: 16px; }

  &_official {
    margin-top: 6px;
    opacity: 0.5;
    color: #ffffff;
    font-size: 13px;
    font-weight: 400; }

  &_call {
    color: #ffffff;
    font-size: 12px;
    font-weight: 300;
    margin-top: 19px;
    text-align: right; }

  &_phone {
    display: block;
    margin-top: 8px;
    color: #ffffff;
    font-size: 22px;
    font-weight: 400;
    text-transform: uppercase;
    text-align: right;
    line-height: 12px; }

  &_btn {
    margin-top: 19px;
    width: 172px;
    height: 46px;
    box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
    border: none;
    border-radius: 4px;
    background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
    text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
    color: #ffffff;
    font-size: 14px;
    font-weight: 400;
    text-transform: uppercase;
    @include btn_hover; } }



.promo {
  min-height: 800px;
  background: url('../img/bg/main_bg.jpg') center (center / cover) no-repeat;
  padding: 150px 0 135px 0;

  &_header {
    color: #ffffff;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 60px;
    line-height: 60px;
    margin: 0;
    text-align: center; }

  &_subheader {
    color: #ffffff;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 36px;
    line-height: 36px;
    margin: 0;
    margin-top: 22px;
    text-align: center; }

  &_descr {
    color: #ffffff;
    font-size: 18px;
    font-weight: 400;
    line-height: 24px;
    text-align: center;
    margin-top: 23px; }

  &_btn {
    display: block;
    margin: 118px auto 0 auto;
    width: 259px;
    height: 63px;
    box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
    border: none;
    border-radius: 4px;
    background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
    text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
    color: #ffffff;
    font-size: 18px;
    font-weight: 400;
    text-transform: uppercase;
    @include btn_hover; } }

.reasons {
  padding: 71px 0 65px 0;

  &_block {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 35px; }

  &_round {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 26px;
    width: 113px;
    height: 113px;
    background-color: #1eacc7;
    border-radius: 100%; }

  &_descr {
    width: 343px; }

  &_subtitle {
    line-height: 21px;
    color: #1eacc7;
    font-size: 17px;
    font-weight: 400; }

  &_text {
    color: #202020;
    font-size: 15px;
    font-weight: 300;
    line-height: 21px; } }

.label {
  margin: 0 auto;
  height: 24px;
  width: 112px;
  padding: 5px 12px;
  background-color: #000000;
  color: #ffffff;
  font-size: 11px;
  font-weight: 300;
  text-transform: uppercase;
  text-align: center;
  &_white {
    background-color: #fff;
    color: black; } }



.title {
  margin-bottom: 0;
  margin-top: 38px;
  color: #222222;
  font-size: 38px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  &_white {
    color: #fff; } }

.subtitle {
  margin-top: 19px;
  color: #222;
  font-size: 17px;
  font-weight: 400;
  text-align: center;
  &_white {
    color: #fff; } }



.mobile {
  min-height: 864px;
  padding: 48px 0 79px 0;
  background: url('../img/bg/mobile_app.png') center (center/cover) no-repeat;
  .row {
    margin-top: 32px; }
  &_item {
    position: relative;
    width: 320px;
    height: 280px;
    margin: 0 auto;
    margin-bottom: 19px;
    padding-top: 92px;
    &_1 {
      background: url('../img/bg/mobile_screen_1.jpg') center (center/cover) no-repeat; }
    &_2 {
      background: url('../img/bg/mobile_screen_2.jpg') center (center/cover) no-repeat; }
    &_3 {
      background: url('../img/bg/mobile_screen_3.jpg') center (center/cover) no-repeat; }
    &_4 {
      background: url('../img/bg/mobile_screen_4.jpg') center (center/cover) no-repeat; }
    &_5 {
      background: url('../img/bg/mobile_screen_5.jpg') center (center/cover) no-repeat; }
    &_6 {
      background: url('../img/bg/mobile_screen_6.jpg') center (center/cover) no-repeat; }
    &_subtitle {
      color: #FFF;
      text-align: center;
      font-size: 24px;
      font-style: normal;
      font-weight: 400;
      line-height: 31px;
      text-transform: uppercase; }
    &_plus {
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      bottom: 35px;
      width: 26px;
      height: 26px;
      background-color: #000000;
      margin: 0 auto;
      border-radius: 100%;
      padding: 6px;
      cursor: pointer;
      &:before {
        content: '';
        display: block;
        width: 14px;
        height: 2px;
        background-color: #1eacc7;
        transition: 0.3 all; }
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        display: block;
        height: 14px;
        width: 2px;
        background-color: #1eacc7;
        transition: 0.3s all; }
      &:hover {
        &:before, &:after {
          background-color: #FFA07A; } } } } }



.car {
  position: relative;
  min-height: 900px;
  padding: 60px 0 140px 0;
  background: linear-gradient(to bottom, #070716 60%, white 40%);
  &_img {
    position: relative;
    display: block;
    margin: 56px auto 0 auto; }

  .under_title {
    color: #222;
    text-align: center;
    font-size: 17px;
    font-weight: 300;
    line-height: 12px; } }


.require {
  padding: 68px 0 84px 0;
  &_block {
    margin: 33px 0;
    &_nmb {
      margin-bottom: 0px; } }
  &_title {
    color: #1CA8C3;
    font-size: 22px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px; }
  &_list {
    list-style-type: none;
    padding-left: 50px;
    margin-top: 25px;
    li {
      position: relative;
      color: #222;
      font-size: 17px;
      font-style: normal;
      font-weight: 300;
      line-height: 24px;
      margin-bottom: 10px;
      &::before {
        content: '';
        display: block;
        position: absolute;
        left: -32px;
        top: 3px;
        width: 21px;
        height: 20px;
        background: url('../icons/check_blue.svg') center (center/cover) no-repeat; } } }
  &_descr {
    color: #1CA8C3;
    font-size: 17px;
    font-style: normal;
    font-weight: 300;
    line-height: 24px; }
  .warning {
    .require_title {
      color: #DC143C; }
    .require_list {
      li {
        &:before {
          background: url('../icons/check_red.svg') center (center/cover) no-repeat; } } } } }


.map {
  position: relative;
  min-height: 720px;
  padding: 48px 0;
  background: url('../img/bg/map.jpg') center (center/cover) no-repeat;
  &_link {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-46%);
    margin-bottom: 22px;
    color: #1FBAD6;
    text-align: center;
    font-size: 15px;
    font-style: normal;
    font-weight: 400;
    line-height: 68px;
    text-transform: uppercase;
    &:hover {
      text-decoration: none; } } }


footer {
  min-height: 345px;
  padding: 14px 23px 39px 23px;
  a {
    &:hover {
      text-decoration: none; } }

  .links {
    width: 390px;
    margin: 0 auto;

    &_main {
      display: flex;
      justify-content: space-between;
      padding: 0 10px;
      font-size: 15px;
      a {
        color: #070716; } }
				font-weight: 300;
				text-transform: uppercase;


    &_main_grey {
      display: flex;
      justify-content: space-between;
      padding: 0 10px;
      font-size: 11px;
      a {
        color: #999999; } }
				font-weight: 300;
				text-transform: uppercase;
    &_bottom {
      display: block;
      margin: 0 auto;
      text-align: center;
      margin-top: 15px;
      color: #1fbad6;
      font-weight: 400;
      text-transform: uppercase;
      font-size: 11px; } } }



.footer_logos {
    width: 160px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    &_item {
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0 4px;
      width: 34px;
      height: 34px;
      background-color: #000;
      border-radius: 100%;
      span {
        color: #fff; } } }


.footer_mobile {
  display: flex;
  justify-content: flex-end; }

.footer_wrapper {
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
	padding-top: 32px; }

.footer_wrapper > div {
  width: 33%;
  &:nth-child(3) {
			display: flex;
			justify-content: flex-end; } }

.xs-visible {
    display: none; }
